<script>    
    import * as lib from './lib'

    export let data

    const criteria = {
        card: ["img", "h1", "p"]
    }

    let filteredClusters = lib.getData(data, criteria)
    
</script>

<div id={data.id}>
    {#each filteredClusters as i, index }
        {#if i.error}

            <p>Div ID: {data.id}, group {index} is missing the following elements: {i.missingElements.join(',')}</p>

        {:else}

            <div>
                {#each i.children as child}
                    {#if child.tag == "img"}
                        <img alt="">
                    {/if}
                    {#if child.tag == "h1"}
                        <h1>{child.text}</h1>
                    {/if}
                    {#if child.tag == "p"}
                        <p>{child.text}</p>
                    {/if}
                {/each}

            </div>

        {/if}      
        
    {/each}
</div>


