<script>
    import * as lib from './lib'

    export let data

    let logo = lib.sortData("img", data)

    let links = lib.sortData("a", data)

</script>

<div id={data.id}>
   
    {#if data.error}

        <p>Div ID: {data.id}, group {data.index} is missing the following elements: {data.missingElements.join(',')}</p>

    {:else}

        <div class="flex w-full h-[80px]">
            <div class="flex w-[200px]">
                <img {...logo.attributes}>
            </div>
            <div class="flex flex-1 justify-end items-center">
                {#each links as i }
                    <a class="p-[25px]" {...i.attributes}>{i.text}</a>
                {/each}
            </div>
        </div>

    {/if}      
        
  
</div>


