<script>
    import { onMount } from "svelte"
    import Gallery from "./Gallery.svelte"
    import TopHeader from "./topHeader.svelte";
    import Header from "./header.svelte";

	//flags
	let pageChecked = false,
		allObjsCreated = false,
		componentsCreated = false
	
	//data holder
	let components = [],
		data = []


	function replaceComps() {
		if (data.length === 0) {
			console.log("No components to replace.")
			return
		}

		for (let i = 0; i < data.length; i++) {
			let component = data[i]
			if (component.id) {

				// Find the component in the DOM using sc-id attribute
				let elementToReplace = document.querySelector(`.sc-id-${component.id}`)

				if (elementToReplace) {
					// Create a new element based on the component data
					let newElement = document.getElementById(component.id)

					// Replace the old component with the new one
					elementToReplace.parentNode.replaceChild(newElement, elementToReplace)
				} else {
					console.log(`Component with sc-id "${component.id}" not found.`)
				}
			}
		}
	}

	function createObjFromComps() {
		function extractAttributes(element) {
			let attributes = {}
			for (let i = 0; i < element.attributes.length; i++) {
				let attribute = element.attributes[i]
				attributes[attribute.name] = attribute.value
			}
			return attributes
		}

		function parseComponent(component) {
			let attributes = extractAttributes(component)
			let children = []

			// Parse children components
			for (let i = 0; i < component.children.length; i++) {
				children.push(parseComponent(component.children[i]))
			}

			let parentSelector = "svelte-comp" // Default parent selector
			let scIdClass = [...component.classList].find(className => className.startsWith("sc-id-"))
			let scTypeClass = [...component.classList].find(className => className.startsWith("sc-type-"))

			if (scIdClass) {
				// Extract the name of the component from "sc-id-'nameofcomp'"
				parentSelector = scIdClass.slice(6)
			}

			// Determine if the component has text content
			let hasText = !children.length && component.textContent.trim().length > 0

			let parsedElement = {
				tag: component.tagName.toLowerCase(),
				attributes: attributes,
				children: children,
				parent: parentSelector,
				id: scIdClass ? scIdClass.slice(6) : null, // Extract id from "sc-id" class
				type: scTypeClass ? scTypeClass.slice(8) : null, // Extract type from "sc-type" class
			}

			// Include text only if the component has no children and has non-empty text content
			if (hasText) {
				parsedElement.text = component.textContent.trim()
			}

			return parsedElement
		}

		function addData(obj) {
			const parsedData = parseComponent(obj)
			data = [...data, parsedData]
		}

		for (let i of components) {
			addData(i)
		}

		allObjsCreated = true

	}		

	function checkPage(){
		let query = document.querySelectorAll('.svelte-comp')
		components = query.length > 0 ? query : []
		pageChecked = components.length > 0 ? true : false 
	}

	function init() {
		onMount(() => {
			checkPage()

			if (pageChecked) {
				createObjFromComps()
			} else {
				console.log('There are no components on the page')
			}		
		})
	}

	init()		

</script>

{#if allObjsCreated}
	{#each data as i, index}
		{#if i.type == "topheader" } <TopHeader data={data[index]} /> {/if}	

		{#if i.type == "header" } <Header data={data[index]} /> {/if}	

		{#if i.type == "gallery"} <Gallery data={data[index]}/> {/if}
	
	{/each}
	<div on:transitionend={replaceComps()}></div>
{/if}



<div class="svelte-comp sc-type-topheader sc-id-theheader">
	
</div>

<div class="svelte-comp sc-type-header sc-id-header">
	<div class="group">
		<div class="left-header">
			<img alt="logo" />
		</div>
		
		<div class="right-header">
			<a href="./">next</a>
			<a href="./">another</a>
		</div>
		
	</div>
	
</div>


<div class="svelte-comp sc-type-gallery sc-id-test">
	<div class="group">
		<img alt="">
		<h1>test</h1>
		<p>another test</p>
	</div>
	<div class="group">
		<img alt="">
		<h1>testing a thing</h1>
		<p>hi</p>
	</div>
</div>


