export function getData(data, criteria){
    return filterClusterData(data, criteria)
}

export function sortData(type, data) {
    let filteredData = []

    // Recursive function to filter objects and process attributes
    function filterObjects(obj) {
        // Check if the object is defined and if its tag matches the specified type
        if (obj && obj.tag && obj.tag.toLowerCase() === type.toLowerCase()) {
            filteredData.push(obj); // Add the object to filtered data
        }
    
        // Recursively check children objects if the object and its children are defined
        if (obj && obj.children && obj.children.length > 0) {
            obj.children.forEach(child => {
                filterObjects(child);
            });
        }
    }
    

    // If data is an array of objects, iterate through each object and call filterObjects
    if (Array.isArray(data.children) && data.children.length > 1) {
        data.children.forEach(item => {
            filterObjects(item)
        });
    } else {
        // If data is a single object, call filterObjects directly
        filterObjects(data.children[0])
    }

    // Return filtered data with objects of the specified type
    return filteredData
}

export function filterClusterData(data, criteria) {
    // Filter div elements from the data
    const divs = sortData('div', data);

    let filteredClusters = [];

    for (let u = 0; u < Object.keys(criteria).length; u++) {
        // Logic to filter clusters based on divs containing img, h1, and p elements and having class "group"
        divs.forEach(div => {
            let hasElements = true; // Assume all elements are present
            let missingElements = [];

            for (let i = 0; i < criteria[Object.keys(criteria)[u]].length; i++) {
                const element = criteria[Object.keys(criteria)[u]][i];
                // Check if the element is present in the div
                if (sortData(element, div).length === 0) {
                    hasElements = false;
                    missingElements.push(element);
                }
            }

            const hasGroupClass = div.attributes.class && div.attributes.class.includes('group');

            if (hasElements && hasGroupClass) {
                filteredClusters.push(div);
            } else if (hasGroupClass) {
                // Group is missing one or more required elements
                filteredClusters.push({ error: `Missing elements in the group: "${missingElements.join(',')}"`, group: div, missingElements: missingElements });
            }
        });
    }

    return filteredClusters;
}


