<script>    
    export let data

    let date = new Date()

    const   day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate(),
            theMonth = date.getMonth() + 1,
            month = theMonth < 10 ? '0' + theMonth : theMonth,
            year = date.getFullYear(),
            currentDate = `${day}-${month}-${year}`
    
    let islamicDate    
    
    const url = `https://api.aladhan.com/v1/gToH/${day}-${month}-${year}`;
    fetch(url)
        .then(data => data.json())
        .then((json) => {
        islamicDate = json.data.hijri.date
        let islamicMonthName = json.data.hijri.month.en
        
        let change = islamicDate.split('-')
        
        change[1] = change[1].replace(change[1], islamicMonthName)
        
        islamicDate = change.join('-')
        
        islamicDate = islamicDate
        
    })
    
</script>



<div class="h-[40px] w-full flex justify-end px-6 text-sm bg-zinc-800" id="{data.id}">
    <p class=" mr-6 select-none flex justify-center items-center text-zinc-400" id="ht-date">{currentDate} | {islamicDate}</p>
    <div class="flex w-[60px] items-center" id="socials"><a class="fa-facebook text-zinc-400 hover:text-zinc-200  transition-all duration-200 fa-brands flex text-[20px] flex-1 justify-center items-center" href="#"></a><a class="fa-instagram text-zinc-400 hover:text-zinc-200  transition-all duration-200 fa-brands flex text-[20px] flex-1 justify-center items-center" href="#"></a>
    </div>
</div>